$iceblue: #4baac5;
$brightblue: #0095ff;
$mossgreen: #2eab9e;

.heading1 {
    color: $mossgreen;
}

.heading2 {
    color: $brightblue;
}

.title {
    color: $brightblue;
}

.venue,
.datetime {
    border-color: lightslategrey;
    background-color: lightslategrey;
}

#name {
    color: $iceblue;
}

html {
    color: #4c566a;
}

a {
    color: $brightblue;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

a>i {
    color: $iceblue;
}

a>i:hover {
    color: #ebcb8b;
}