* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.heading1 {
  font-size: 1.2rem;
  font-weight: 700;
}

.heading2 {
  font-size: 1.1rem;
  padding: .5rem 0;
}

.item {
  padding: .5rem;
}

.title {
  font-size: 1.1rem;
}

.author {
  font-size: .9rem;
}

.venue,
.datetime {
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  padding: 0 .2rem;
  color: white;
  font-size: .8rem;
  font-weight: 600;
}

.workshop {
  font-size: .9rem;
}

#name {
  font-size: 1.5rem;
  font-weight: 700;
}

#sidebar {
  border-right: 5px solid #eceff4;
  padding: 4rem 3rem 1rem 1rem;
  font-size: .9rem;
  width: 270px;
  position: fixed;
  height: 100%;
  overflow: auto;
}

#sidebar>div {
  padding: .6rem 0;
}

#shortcuts {
  margin-top: .6rem;
  text-align: center;
}

#shortcuts>a {
  padding: 0 .2rem;
}

#copyright {
  font-size: .9rem;
  text-align: center;
  padding: 3rem 0 1rem 0;
}

#contents {
  margin-left: 300px;
  padding: 4rem 1rem 1rem 1rem;
}

#contents>div {
  padding-bottom: 2rem;
}

#container {
  margin: 0 auto;
  max-width: 1000px;
}

img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 1rem;
}

html {
  font-size: 1em;
  height: 100%;
  font-family: Segoe UI, Frutiger, Frutiger Linotype, Dejavu Sans, Helvetica Neue, Arial, sans-serif;
}

body {
  min-height: 100vh;
  margin: 0 auto;
}

@media screen and (max-width: 700px) {
  img {
    display: none;
  }

  #sidebar {
    padding: 1rem;
    width: 100%;
    height: auto;
    position: relative;
  }

  #contents {
    margin: 0;
    padding: 1rem;
  }
}